html, body, #root{
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  color: rgba(0,0,0,.87);
  background: #F7F7F7;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Text-input{
    margin: 0;
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    border: 1px solid rgba(34, 36, 38, .15);
    border-radius: 5px;
    outline: none;
    padding: 10px;
    background: #fff;
    text-align: center;
    color: rgba(0, 0, 0, .87);
    font-size: 100%;
    box-shadow: inset 1px 1px 0 rgba(34,36,38,.15);
}

.Text-input:focus{
    border-color: #85b7d9;
    background: #fff;
    color: rgba(0, 0, 0, .8);
}

.Text-input:focus::-webkit-input-placeholder {
    opacity: 0;
}

.Text-input:focus::-ms-input-placeholder {
    opacity: 0;
}

.Text-input:focus::placeholder {
    opacity: 0;
}
.Button{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 39px;
    min-width: 100px;
    background: #00B4AD;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    border-radius: 5px;
    cursor: pointer;
}

.Button.forward{
    background: #00B4AD;
}

.Button.back{
    background: #DA2728;
}
.Button.disabled{
    opacity: 0.5;
}
.LeaderBoard-grid {
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
    border: 1px solid rgba(34,36,38,.15);
    border-radius: 5px;
    overflow: hidden;
}

.LeaderBoard-grid > span {
    font-weight: 700;
    padding: 8px;
    border-bottom: 1px solid #EAEAEA;
}
.LeaderBoard-row > span {
    padding: 8px;
    border-left: 1px solid #EAEAEA;
    border-bottom: 1px solid #EAEAEA;
}
.LeaderBoard-row > span:first-child {
    border-left: none;
}
.LeaderBoard-row_number, .LeaderBoard-row_icon {
    text-align: center;
}

.LeaderBoard-row_icon {
    cursor: pointer;
}

.LeaderBoard-row_icon.edit {
    color: #00B4AD;
}

.LeaderBoard-row_icon.delete {
    color: #DA2728;
}
.LeaderBoard-add{
    font-weight: 700;
}

.LeaderBoard-add._success{
    color: #00b5ad
}

.LeaderBoard-add._error, .LeaderBoard-add._exists{
    font-weight: 700;
    color: #db2828;
}
